@media (max-width: 575.98px) {
	// INDEX
	p.recuo-primeira-linha {
		text-indent: 0;
	}

	.rodape-index {
		padding-bottom: 10px;
	}
	
	.voltar-topo {
		padding-bottom: 50px;
	}

	#video {
		width: 345px;
		height: 213px;
	}

	// INSTITUCIONAL
	.conteudo {
		padding-bottom: 100px;
	}

	.carousel-item {
		height: 650px;
	}
}

/*Extra small devices (portrait phones, menor que 576px)*/
@media (min-width: 576px) and (max-width: 767.98px) {
	
	#video {
		width: 460px;
		height: 355px;
	}

	.voltar-topo {
		padding-bottom: 35px;
	}

	// INSTITUCIONAL
	.conteudo {
		padding-bottom: 75px;
	}	
}

/*Small devices (landscape phones, 576px e maiores)*/
@media (min-width: 768px) and (max-width: 990.98px) {
	
	p.recuo-primeira-linha {
		text-indent: 20px;
	}

	.voltar-topo {
		padding-bottom: 50px;
	}

	// INSTITUCIONAL
	.conteudo {
		padding-bottom: 75px;
	}
}

/*Medium devices (tablets, 768px e maiores)*/
@media (min-width: 991px) and (max-width: 1199.98px) {
	
	p.recuo-primeira-linha {
		text-indent: 40px;
	}
	
	#video {
		width: 576px;
		height: 444px;
	}

	.voltar-topo {
		padding-bottom: 20px;
	}
}

/*Large devices (desktops, 992px e maiores)*/
@media (min-width: 1200px) {
	
	p.recuo-primeira-linha {
		text-indent: 50px;
	}

	.voltar-topo {
		padding-bottom: 30px;
	}
}

