// RANGE
input[type=range] {
	height: 28px;
	-webkit-appearance: none;
	margin: 10px 0;
	width: 100%;
  }
  input[type=range]:focus {
	outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 20px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #002200;
	background: #999999;
	border-radius: 20px;
	border: 0px solid #28A745;
  }
  input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px #001700;
	border: 2px solid #85E886;
	height: 20px;
	width: 50px;
	border-radius: 20px;
	background: #28A745;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -1px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
	background: #999999;
  }
  input[type=range]::-moz-range-track {
	width: 100%;
	height: 20px;
	cursor: pointer;
	animate: 0.2s;
	box-shadow: 0px 0px 0px #002200;
	background: #999999;
	border-radius: 20px;
	border: 0px solid #28A745;
  }
  input[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 0px #001700;
	border: 2px solid #85E886;
	height: 20px;
	width: 50px;
	border-radius: 20px;
	background: #28A745;
	cursor: pointer;
  }
  input[type=range]::-ms-track {
	width: 100%;
	height: 20px;
	cursor: pointer;
	animate: 0.2s;
	background: transparent;
	border-color: transparent;
	color: transparent;
  }
  input[type=range]::-ms-fill-lower {
	background: #999999;
	border: 0px solid #28A745;
	border-radius: 40px;
	box-shadow: 0px 0px 0px #002200;
  }
  input[type=range]::-ms-fill-upper {
	background: #999999;
	border: 0px solid #28A745;
	border-radius: 40px;
	box-shadow: 0px 0px 0px #002200;
  }
  input[type=range]::-ms-thumb {
	margin-top: 1px;
	box-shadow: 0px 0px 0px #001700;
	border: 2px solid #85E886;
	height: 20px;
	width: 50px;
	border-radius: 20px;
	background: #28A745;
	cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
	background: #999999;
  }
  input[type=range]:focus::-ms-fill-upper {
	background: #999999;
  }




// FLIPSWITCH

.flipswitch {
	position: relative;
	width: 127px;
  }
  .flipswitch input[type=checkbox] {
	display: none;
  }
  .flipswitch-label {
	display: block;
	overflow: hidden;
	cursor: pointer;
	border: 1px solid #999999;
	border-radius: 19px;
  }
  .flipswitch-inner {
	width: 200%;
	margin-left: -100%;
	transition: margin 0.3s ease-in 0s;
  }
  .flipswitch-inner:before, .flipswitch-inner:after {
	float: left;
	width: 50%;
	height: 20px;
	padding: 0;
	line-height: 20px;
	font-size: 16px;
	color: white;
	font-family: Trebuchet, Arial, sans-serif;
	font-weight: bold;
	box-sizing: border-box;
  }
  .flipswitch-inner:before {
	content: "ATIVO";
	padding-left: 20px;
	background-color: #28A745;
	color: #FFFFFF;
  }
  .flipswitch-inner:after {
	content: "INATIVO";
	padding-right: 20px;
	background-color: #EBEBEB;
	color: #888888;
	text-align: right;
  }
  .flipswitch-switch {
	width: 20px;
	margin: 0px;
	background: #FFFFFF;
	border: 1px solid #999999;
	border-radius: 19px;
	position: absolute;
	top: 0;
	bottom: 0;
	right: 105px;
	transition: all 0.3s ease-in 0s;
  }
  .flipswitch-cb:checked + .flipswitch-label .flipswitch-inner {
	margin-left: 0;
  }
  .flipswitch-cb:checked + .flipswitch-label .flipswitch-switch {
	right: 0;
  }


